export default {
	data() {
		return {
			len:0,
			startX:0,
			moveEndX:0,
			valueX:0,
			vervisit:'none',
			vershow:false,
			indexnum:1,
			bindingif:false
		};
	},
		methods:{
				touchstart(event){
					event.preventDefault();
	　　　　			this.startX = event.touches[0].pageX
				},
				touchmove(event){
					event.preventDefault();
			　　　　this.moveEndX = event.touches[0].pageX
					this.valueX= this.moveEndX - this.startX
					if ( this.valueX >= 80 ) {
							uni.navigateBack({
								delta:1,
								animationType: 'pop-in',
								animationDuration: 200
							})　	
			　　　　}
				},
			binding(event){
				if(this.indexnum==1){
					this.indexnum=0
					this.vervisit="block"
					this.vershow=true
					setTimeout(()=>{
						this.indexnum=1
						this.vervisit="none"
						this.vershow=false
					},2000)
				}
			}
		}
}
